import React, {useState, useEffect} from 'react';
import ArticleList from './ArticleList';
import Category from '../../../common/Category';
import {API_URL, API_KEY} from '../../../../Config';
import '../../../common/news.scss';
import {NewsTypes} from '../../../common/types/NewsType';
import axios from 'axios';
import {useAppDispatch} from '../../../../store';
import {addRecentNews} from '../../../../store/news';

const News: React.FC = () => {
  const dispatch = useAppDispatch();
  const [category, setCategory] = useState([
    {category: 'news', name: '', isClicked: true},
    {category: 'business', name: '', isClicked: false},
    {category: 'entertainment', name: '', isClicked: false},
    {category: 'politics', name: '', isClicked: false},
    {category: 'economics', name: '', isClicked: false},
    {category: 'science', name: '', isClicked: false},
    {category: 'sport', name: '', isClicked: false},
    {category: 'tech', name: '', isClicked: false},
  ]);
  const [curCategoryIdx, setCurCategoryIdx] = useState(0);
  const [select, setSelect] = useState('');
  const [news, setNews] = useState<NewsTypes[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [loading, setLoading] = useState(true);
  const options = {
    method: 'GET',
    url: `${API_URL}latest_headlines`,
    params: {
      countries: 'KR',
      lang: 'ko',
      topic: category[curCategoryIdx].category,
      page_size: 50,
    },
    headers: {
      'x-api-key': API_KEY,
    },
  };

  useEffect(() => {
    fetchNews(options);
    setLoading(true);
  }, [category]);

  const fetchNews = async (options: object) => {
    try {
      const response = await axios.request(options);
      if (isLoading)
        dispatch(addRecentNews(response.data.articles.slice(0, 10)));
      setNews([...response.data.articles]);
      setIsLoading(false);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="news">
      <Category
        category={category}
        setCategory={setCategory}
        setCurCategoryIdx={setCurCategoryIdx}
        select={select}
        setSelect={setSelect}
      />
      <ArticleList
        curCategory={category[curCategoryIdx].name}
        news={news}
        loading={loading}
      />
    </div>
  );
};

export default News;
